    <p>&nbsp;스트레스가 미각에 미치는 영향</p><p><br /></p><p>어떤 사람은 스트레스를 받아서 살이 쪘다고 하죠.</p><p>그리고 어떤 사람은 스트레스를 받아서 살이 빠졌다고 합니다.</p><p>이건 뭔가요?</p><p><br /></p><p><i><u>스트레스가 심해지면 아드레날린이 과도하게 분비돼 혈류에 장애가 일어나고,</u></i></p><p><i><u>침이 충분히 분비되지 않아 미각을 제대로 느끼지 못하게 된다.</u></i></p><p style="text-align: left;">&lt;도서 [미각력] 중에서&gt;</p><p style="text-align: left;"><br /></p><p style="text-align: left;">그렇습니다.</p><p style="text-align: left;">한 마디로 스트레스를 받으면 미각이 둔해진다는 것이네요.</p><p style="text-align: left;">미각이 둔해지면 음식을 대할 때, 사람의 행동은 두 가지로 나타나겠고요.</p><p style="text-align: left;"><br /></p><p style="text-align: left;">[더 자극적인 음식을 찾거나]</p><p style="text-align: left;">&nbsp; =&gt; "당 떨어져", "화끈하게 매운거 묵자", "(습관적으로)술이나 한잔 빠라삐리뽀?!"</p><p style="text-align: left;">[밥 맛이 없어서 식사를 거르거나]</p><p>&nbsp; =&gt; "요즘 밥 맛이 통 없네..", "잠이나 자자", "(습관적으로)술이나 한잔...."</p><p><br /></p><p>습관이나 성향에 따라서 사람들은 각자 선택을 하겠지만, 두 가지 다 좋아보이진 않네요.</p><p>하지만, 원인은 확실합니다.</p><p><br /></p><p>스트레스</p><p><br /></p><p>스트레스 = 힘들다 = 괴롭다 = 의욕이 없어진다</p><p>연상되는 말들 아닌가요?</p><p><br /></p><p>이런 말들은 결국 건강을 해치게 되는 것이겠죠. (그리고 살도 찌죠.)</p><p><br /></p><p>날씬한 내가 되기 위해서라도 스트레스를 덜 받아야하고,&nbsp;</p><p>스트레스가 밀려오는 것 같으면, 쌓아두지 말고 바로바로 풀어주는 것이 중요합니다.</p><p>건강 해치기 전에요. (살 찌기 전에요.)</p><p></p><div class="separator" style="clear: both; text-align: left;"><a href="https://lh3.googleusercontent.com/-wb_0uTEKto8/X6fhD0iRiSI/AAAAAAAApCk/EKroMh5RVJIOqOYXI86S1M2nDBE7fTaOwCLcBGAsYHQ/image.png" style="margin-left: 1em; margin-right: 1em;"><img alt="" data-original-height="703" data-original-width="831" height="240" src="https://lh3.googleusercontent.com/-wb_0uTEKto8/X6fhD0iRiSI/AAAAAAAApCk/EKroMh5RVJIOqOYXI86S1M2nDBE7fTaOwCLcBGAsYHQ/image.png" width="284" /></a></div><p></p><p>우리 모두 스트레스와 맞닥뜨리게 되면, 바로 날려버리도록 해요.</p><p>꼭 거금을 들여서 멀리 떠나지 않아도 괜찮아요.</p><p><br /></p><p>친구와 대화를 하거나, 자기 스스로와 대화를 하세요.</p><p><br /></p><p>지인과 대화할 땐, 서로를 존중하며 차분히 주고받는 대화를 할 수 있는 환경이면 좋겠죠.</p><p>진솔한 술 한잔(정말 딱 한 잔 기분 좋은), 또는 Coffee나 Tea 한 잔 곁들여도 좋아요.</p><p></p><div class="separator" style="clear: both; text-align: left;"><a href="https://lh3.googleusercontent.com/-KXexXpWyjYQ/X6fibUjoQ1I/AAAAAAAApCw/d80Hdyv2fGc3W2Vlg6e5SkUAYcFdB6nhwCLcBGAsYHQ/image.png" style="margin-left: 1em; margin-right: 1em;"><img alt="" data-original-height="937" data-original-width="527" height="320" src="https://lh3.googleusercontent.com/-KXexXpWyjYQ/X6fibUjoQ1I/AAAAAAAApCw/d80Hdyv2fGc3W2Vlg6e5SkUAYcFdB6nhwCLcBGAsYHQ/w180-h320/image.png" width="180" /></a></div><br /><br /><p></p><p>그런데...</p><p>자기와의 대화는 오글스럽게 뭐냐고요?</p><p><br /></p><p>일기나 편지를 쓰면 되죠.</p><p>혹자는,&nbsp;</p><p>"뭘 그렇게까지. 메신져, 전화로 수다떨고 풀어버리지."</p><p>"거하게 술 마시고 해장하면서 풀어버리면 되지 뭘 그렇게까지? 가자!"</p><p>라고들 하십니다.</p><p>하지만 그건 그야말로 "풀어"내서 "버리"는 것 아닐까요?</p><p>물론 순간적으로 스트레스가 날아가는 희열과 환희가 느껴질지도 몰라요.</p><p>아니 스트레스를 잠시 망각하는 것이겠죠.</p><p><br /></p><p>그건 내 마음으로 무례하게 쳐들어온 불편함은 잠시 날려버릴 순 있어도,&nbsp;</p><p>본질적인 고민은 아직 내면에 숨어있을지도 몰라요.</p><p>(참고로, 서로간의 대화 유연성 확보가 목적인 자리(회식, 뒷풀이 등)라면, 그건 좀 다른 이야기입니다. 스트레스 풀려고 술 마시지 말자는 거예요.)</p><p><br /></p><p>불쌍하게 혼자 남아 구석에서 울고있을 나의 고민에게..</p><p>직접 다가가 정중하게 편지 한 번 써보세요.</p><p>어색하다면, 일기는 어떠세요?</p><p><br /></p><p>어떤 형식이든 나를 괴롭히는 어떤 대상과 마주하고 대화하는 것이 중요합니다.</p><p>자신을 점점 더 잘 알아가는 과정이니, 절대 시간 낭비하는 것은 아닐 것입니다.</p><p>미래의 자신에 대한 투자라고 생각하면, 오히려 기회일지도 모르죠.</p><p><br /></p><p>스트레스를 마주한다는 것은 그만큼 몸과 마음에서 신호를 보내주었기 때문입니다.</p><p>지금 뭔가 잘못된 길로 가고있는 것 아니냐고.</p><p>뭔가 마음이 불편하지 않냐고, 스스로가 잘 알지 않냐고.</p><p>잠깐 좀 멈춰야 하는 것 아니냐고.</p><p><br /></p><p>우리는 어른 흉내를 내면서, 애써 안 힘든척, 강한척.&nbsp;</p><p>그렇게 척을하며 외면하지만, 여러분.</p><p>스트레스를 무시하면 건강 해칩니다. (살 찝니다.)</p><p><br /></p><p>잘 만나세요.</p><p>그리고 잘 화해하세요.</p><p><br /></p><p>그래서, 자신과 더 친해지고 돈독해지세요.</p><p>그렇게 자존감을 느끼고 성장하세요.</p><p>그게 자기 삶을 살아가는 건강한 모습인 것 같습니다.</p><p><br /></p><p>여러분의 용기와 건투를 빕니다.</p><p><br /></p>
