import { createWebHistory, createRouter } from 'vue-router'
import Home from '../components/Home'
import Left from '../components/Left'


import Menu from '../components/Menu'

import Menu1 from '../components/Menu1'
import Menu1_Nav from '../components/Menu1_Nav'
import Menu1_1 from '../components/Menu1_1'
import Menu1_2 from '../components/Menu1_2'
import Menu1_3 from '../components/Menu1_3'

import Menu2 from '../components/Menu2'
import Menu2_Contents from '../components/Menu2_Contents'
import Menu2_1 from '../components/Menu2_1'

// import Nav from '../components/Nav'
import Page1 from '../components/Page1'
import Page2 from '../components/Page2'
import DigitalClock from '../components/DigitalClock.vue'

// const Home = { template: "<div>Home</div>" }
// const NotFound = { template: "<div>Not Found</div>" }

const routes = [
    {
        path: '/',          // 경로
        name: 'home',           // 해당 경로의 이름(참고)
        component: Home,     // 이동할 컴포넌트(router-view 영역에 보여줄 컴포넌트를 지정)
    },
    // {
    //     path: '*',          // 경로
    //     name: 'home',           // 해당 경로의 이름(참고)
    //     component: NotFound,     // 이동할 컴포넌트(router-view 영역에 보여줄 컴포넌트를 지정)
    // },
    {
        path: '/left',
        name: 'left',
        component: Left,
    },
    { path: '/menu', name: 'menu', component: Menu },

    { path: '/menu1', name: 'menu1', component: Menu1 },
    { path: '/menu1_nav', name: 'menu1_nav', component: Menu1_Nav },
    { path: '/menu1_1', name: 'menu1_1', component: Menu1_1 },
    { path: '/menu1_2', name: 'menu1_2', component: Menu1_2 },
    { path: '/menu1_3', name: 'menu1_3', component: Menu1_3 },
    

    { path: '/menu2', name: 'menu2', component: Menu2 },
    { path: '/menu2_contents', name: 'menu2_contents', component: Menu2_Contents },
    { path: '/menu2_1', name: 'menu2_1', component: Menu2_1 },

    // {
    //     path: '/nav',
    //     name: 'nav',
    //     component: Nav,
    // },
    {
        path: '/page1',
        name: 'page1',
        component: Page1,
    },
    {
        path: '/page2',
        name: 'page2',
        component: Page2,
    },
    {
        path: '/clock',
        name: 'clock',
        component: DigitalClock,
    },
];

const router = createRouter({
    history: createWebHistory(),
    routes,
});

export default router;
